import React, { memo } from "react";
import Ruby from "../../components/atoms/Ruby/Ruby";
import { CSImage } from "../../components/atoms/CSImage/CSImage";
import SetPageNum from "../../Types/SetPageNum";
import { ImgWithCountDown } from "../../components/molecules/ImgWithCountDown/ImgWithCountDown";
import QuizPageProps from "../../Types/QuizPageProps";
import { RescureDescription } from "../../components/templates/RescureDescription/RescureDescription";

export interface Description1Props extends SetPageNum, QuizPageProps {
}

/**
 * Functional component.
 * @param props 
 */
const Description1: React.FC<Description1Props> = memo(({
  setPageNum,
  isMute
}) => {
  return (
    <>
      <RescureDescription
        setPageNum={ setPageNum }
        isMute={ isMute }
        audioPath="/05.mp3"
        countMax={ 7 }
        description={ <>
          あなたの<Ruby ruby="なまえ">名前</Ruby>、<Ruby ruby="れんらくさき">連絡先</Ruby>を<Ruby ruby="おし">教</Ruby>えてください。<br />
          <Ruby ruby="ただ">直</Ruby>ちに<Ruby ruby="じゅんしせんてい">巡視船艇</Ruby>、<Ruby ruby="こうくうき">航空機</Ruby>を<Ruby ruby="む">向</Ruby>かわせます。
        </> }
      >
        <CSImage
          desc="海上保安庁"
          maxWidth={ {
            pc: 576,
            tb: 475,
            sp: 203
          } }
        >
          <ImgWithCountDown
            image="koudou_rescue_go118.png"
            ratio={ 68.354 }
          >
            {
              /**
               * <CountDownBlue count={ count }/>
               */
            }
          </ImgWithCountDown>
        </CSImage>
      </RescureDescription>
    </>
  );
});

export default Description1;
